/* Turn this into a modifier for modal */
.o-preview {

  input,
  label,
  textarea {
    pointer-events: none;
    user-select: none;
  }

  .c-modal__backdrop {
    @media (--md-up) {
      left: var(--sidenav-width);
      top: var(--header-height);
    }
  }

  .c-modal__content {
    padding: 10px 18px 16px;
  }

  .c-modal__card {
    @media (--md-up) {
      margin-left: 7em;
      min-width: 400px;
    }
  }

  section {
    background-color: var(--white);
    border-radius: 4px;
    box-shadow: 0 0 3px rgba(0, 0, 0, .5);
    margin-bottom: 16px;
    padding: 16px;
  }

  small {
    color: var(--blue-800);
    margin-bottom: 2em;
  }

  .o-hang {
    display: inline-block;
    float: right;
    overflow: hidden;
    width: 90%;
  }
}
