/*---
title: Size
section: Elements
---*/

:root {
  --global-line-height: 32px;
  --global-duration: .1s;
  --global-margin: 8px;
  --global-rounded: 4px;
  --global-z-index: 100;
  --frame-quarter: calc(var(--global-line-height) / 4); /* 8px */
  --frame-third: calc(var(--global-line-height) / 3); /* 10.6px */
  --frame-half: calc(var(--global-line-height) / 2); /* 16px or 1em */
  --frame: var(--global-line-height); /* 32px */
  --frame-double: calc(var(--global-line-height) * 2); /* 64px */
  --frame-triple: calc(var(--global-line-height) * 3); /* 96px */
}

.u-leader--none { margin-top: 0 !important; }
.u-leader--tenth { margin-top: calc(var(--global-line-height) * .1) !important; }
.u-leader--eighth { margin-top: calc(var(--global-line-height) * .125) !important; }
.u-leader--sixth { margin-top: calc(var(--global-line-height) * .1666) !important; }
.u-leader--fifth { margin-top: calc(var(--global-line-height) * .2) !important; }
.u-leader--quarter { margin-top: calc(var(--global-line-height) * .25) !important; }
.u-leader--third { margin-top: calc(var(--global-line-height) * .3) !important; }
.u-leader--half { margin-top: calc(var(--global-line-height) * .5) !important; }
.u-leader { margin-top: var(--global-line-height) !important; }
.u-leader--double { margin-top: calc(var(--global-line-height) * 2) !important; }
.u-leader--triple { margin-top: calc(var(--global-line-height) * 3) !important; }
.u-trailer--tenth { margin-bottom: calc(var(--global-line-height) * .1) !important; }
.u-trailer--eighth { margin-bottom: calc(var(--global-line-height) * .125) !important; }
.u-trailer--sixth { margin-bottom: calc(var(--global-line-height) * .1666) !important; }
.u-trailer--fifth { margin-bottom: calc(var(--global-line-height) * .2) !important; }
.u-trailer--quarter { margin-bottom: calc(var(--global-line-height) * .25) !important; }
.u-trailer--third { margin-bottom: calc(var(--global-line-height) * .3) !important; }
.u-trailer--half { margin-bottom: calc(var(--global-line-height) * .5) !important; }
.u-trailer { margin-bottom: var(--global-line-height) !important; }
.u-trailer--double { margin-bottom: calc(var(--global-line-height) * 2) !important; }
.u-trailer--triple { margin-bottom: calc(var(--global-line-height) * 3) !important; }
.u-width--full { width: 100%; }
