/*---
title: Editor
section: Objects
---*/

.o-editor {
  @apply --card-wide;
  box-shadow: none;
  border: 1px solid rgba(255, 255, 255, 0);
  border-radius: var(--global-rounded);
  padding: 0;
  transition: border .1s ease;
  overflow: visible;

  @media (--md-up) {
    overflow: hidden;

    &:hover {
      border: 1px solid var(--blue-200);

      & > .o-editor__toolbar .o-editor__actions { @apply --fade-in; }
    }
  }

  &:focus {
    border: 1px solid var(--blue-200);
    & > .o-editor__toolbar .o-editor__actions { @apply --fade-in; }
  }

  &:not(.is-active) .o-editor__done { display: none; }

  &:--active {
    border: 1px solid var(--blue-200);

    & > .o-editor__toolbar,
    & > .o-editor__toolbar .o-editor__actions,
    & > .o-editor__content .o-editor__conditionals,
    & > .o-editor__toolbar .o-editor__nav,
    & > .o-editor__toolbar .o-editor__extras { @apply --fade-in; }

    & > .o-editor__content .o-editor__conditionals {
      height: auto;
      margin-bottom: 1em;
    }

    & > .o-editor__toolbar { height: 36px; }
  }

  & & {
    margin-bottom: 3em;

    @media (--md-up) { margin-bottom: 1em; }
  }
}

.o-editor__toolbar {
  @apply --row;
  background-color: var(--blue-200);
  height: 0;

  .c-input-label {
    display: inline-block;
    margin: 5px 10px 0 0;
  }
}

.o-editor__nav,
.o-editor__info {
  @apply --width-column-auto;
  padding: 0;
}

.o-editor__info { @apply --end; }

.o-editor__extras { display: inline-block; }

.o-editor__toolbar--small {
  @apply --middle;
  height: 24px !important;
  padding: 0 5px;

  .c-button {
    height: 25px;
    line-height: 25px;
    padding: 0;
    margin: 0;
  }

  .c-checkbox + label { margin: 0; }
}

.o-editor:not(.is-active) {
  .o-editor__nav,
  .o-editor__extras,
  .o-editor__toolbar--small {
    @apply --fade-out;
    height: 0 !important;
  }
}

.o-editor__actions {
  @apply --fade-out;
  @apply --center;
  background-color: var(--blue-800);
  margin: 0 -1px;
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  @media (--md-up) {
    @apply --start;
    background-color: transparent;
    margin: 0;
    padding-top: 0;
    position: static;
  }

  .c-button {
    border-radius: 0;
    font-size: var(--fontSize--small) !important;
    text-transform: uppercase;
  }

  .c-icon { color: var(--blue-100); }

  .o-editor:not(.is-active) & .o-editor__done,
  .o-editor:not(.is-active) & .o-editor__edit {
    border-bottom-left-radius: var(--global-rounded) !important;
    border-top-left-radius: 0;
  }

  .is-active & .o-editor__edit {
    display: none;
  }
}

.o-editor__more-action { margin-right: .5rem; }

.o-editor__content {
  @apply --start;
  padding: .75em .75rem;
  margin-bottom: 0;

  & .c-card:last-child {
    margin-bottom: 0;
  }
}

.o-editor__conditionals {
  @apply --row;
  @apply --row-collapse;
  @apply --middle;
  @apply --fade-out;
  height: 0;
}


.c-button--favorite {
  background-color: transparent;
  box-shadow: none;
  color: var(--blue-700);
  font-size: var(--fontSize--small) !important;
  margin: 0 6px 0 0 !important;
  padding: 8px 0 !important;
  position: relative;
  text-transform: uppercase;

  .c-icon {
    bottom: 1.5px;
    fill: var(--blue-700);
    height: 23px;
    position: relative;
    transition: color .1s ease-in-out;
    width: 23px;
  }

  &::before {
    background-color: transparent;
    border-radius: 50%;
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    content: '';
    display: inline-block;
    height: 8px;
    left: 8px;
    position: absolute;
    top: 16px;
    transition: box-shadow .1s ease;
    width: 8px;
  }

  &:--active {
    background-color: transparent;
    box-shadow: none;

    &::before { box-shadow: 0 0 10px rgba(0, 0, 0, .72), 0 0 10px rgba(0, 0, 0, .72); }

    .c-icon { fill: var(--gold-200); }
  }
}
