/*---
section: Utilities
title: Visibility
---*/

:root {
  --fade-in: {
    opacity: 1;
    pointer-events: all;
    transition: opacity var(--global-duration) ease-in-out;
    visibility: visible;
    z-index: 10;
  };
  --fade-out: {
    opacity: 0;
    pointer-events: none;
    transition: visibility 0s 2s, opacity var(--global-duration) ease-in-out, z-index 0s 2s;
    visibility: hidden;
    z-index: -1
  };
  --hide-a11y: {
    height: 0;
    opacity: 0;
    position: absolute;
    width: 0;
    z-index: -1;
  };
  --hide-text: {
    text-indent: -99999em;
    white-space: nowrap;
  };
}

.u-fade-out { @apply --fade-out; }
.u-fade-in { @apply --fade-in; }

.u-hide,
[hidden] { display: none !important; }

[hidden][aria-hidden='false'],
.u-show\@reader {
  clip: rect(0 0 0 0);
  display: inherit;
  height: 1px;
  overflow: hidden;
  position: absolute !important;
  width: 1px;

  &:focus { clip: auto; }
}

