/*---
title: Listing
section: Objects
---*/

/* Listing - Basic */

:root {
  --listing-border: var(--grey-600);
  --listing-action-color: var(--grey-700);
  --listing-action-active-color: var(--color-page);
}

.u-column--icons { padding-left: 0 !important; }

.o-listing {
  @apply --row;
  border-bottom: 1px solid var(--listing-border);
  position: relative;
  transition: background-color .3s ease-in-out, box-shadow .3s ease-in-out;
  &:first-child {
    border-top: 1px solid var(--listing-border);
  }

  &:focus,
  &.is-active {
    /* box-shadow: 0 1px 10px rgba(0, 0, 0, .5); */
    outline: none;
    z-index: 5;
  }

  &__status {
    cursor: pointer;
    padding: .5em .5rem;
    width: 40px;
  }

  &__info {
    @apply --width-column-10;
    @media (--md-up) {
      @apply --width-column-auto;
    }
  }

  &__wrapper {
    @apply --top;
    @apply --row;
    cursor: pointer;
    min-height: 48px;
    padding: .25em 0;
  }

  &__general {
    @apply --width-column-auto;
    @media (--md-up) {
      @apply --width-column-4;
    }
  }

  &__name {
    display: inline-block;
    font-weight: bold;
    margin-bottom: 0 !important;
    margin-top: 0 !important;
  }

  &__duration { text-transform: uppercase; }
  @media (--md-up) {
    &__duration {
      @apply --end;
      @apply --width-column-4;
    }
  }

  &__actions {
    position: absolute;
    right: 3px;
    top: 3px;

    &.is-active {
      @apply --fade-in;
    }

    .c-icon {
      background-color: transparent;
      color: var(--listing-action-color);
      cursor: pointer;
      transition: color .1s ease-in-out;

      &:focus {
        background-color: var(--white);
        color: var(--listing-action-active-color);
      }

      @media (--lg-up) {
        &:hover {
          background-color: var(--white);
          color: var(--listing-action-active-color);
        }
      }

      &.is-active,
      &:active {
        background-color: var(--grey-300);
        border-radius: 50px;
      }
    }
  }

  &:hover,
  &.is-active {
    .o-listing__action-group > .c-button--icon {
      @apply --fade-in;
    }
  }

  &__menu {
    max-width: 34px;
    position: relative;
    z-index: 2;
  }

  &__action-group {
    display: inline-block;
    padding: 4px;

    & > .c-button--icon {
      @apply --fade-out;
      display: none;
      @media(--md-up) {
        display: inherit;
      }
    }
  }
  @media (--sm-down) {
    &__action-group &__action:not(.o-listing__menu) {
      display: none;
    }
  }

  &__menu-group {
    @apply --end;
    background-color: var(--white);
    font-size: var(--fontSize--semiNormal);
    margin-right: .5rem;
    min-width: 130px;
    @media (--md-up) { @apply --fade-out; }

    &.is-active { @apply --fade-in; }

    .o-listing__action {
      display: block;
      padding: 5px;
      text-align: left;
      width: 100%;
    }
  }

  &__stats {
    @apply --center;
    @apply --width-column-4;
    margin: .5em 0;

    .o-label:nth-child(odd) { font-weight: bold; }
  }

  &__delivery { @apply --width-column-2; }
}

/* -- Listing - Detail */

.o-listing__info--detail {
  .o-listing__heading {
    border-bottom: 1px solid var(--grey-600);
    display: inline-block;
    font-size: var(--fontSize--tiny);
    font-weight: bold;
    text-transform: uppercase;
    width: 100%;
    & + ul {
      font-size: var(--fontSize--small);
      margin-bottom: .75em;
    }
  }
}
