/*---
title: Checkbox
section: Components
---*/

.c-checkbox { @apply --hide-a11y; }

.c-checkbox:checked + label {
  &::before { @apply --fade-in; }
  &::after {
    background-color: var(--green-600);
    border: 2px solid var(--green-600);
  }
}

.c-checkbox + label {
  cursor: pointer;
  display: inline-block;
  font-size: var(--fontSize--small);
  margin-right: 5px;
  position: relative;

  &::before,
  &::after {
    content: '';
    display: block;
  }

  &::before {
    @apply --checkmark;
    @apply --fade-out;
    position: absolute;
    left: 5px;
    top: 5px;
    z-index: 1;
  }

  &::after {
    border: 2px solid var(--grey-600);
    border-radius: 2px;
    float: left;
    height: 14px;
    margin-right: 8px;
    margin-top: 4px;
    position: relative;
    transition: border var(--global-duration) ease;
    width: 14px;
  }
}

.c-checkbox:focus + label,
.c-checkbox + label:active,
.c-checkbox:checked + label:active {
  box-shadow: var(--toggle-focus-shadow);
}

.c-checkbox:indeterminate + label,
.c-checkbox.is-indeterminate + label,
.c-checkbox:indeterminate:checked + label,
.c-checkbox.is-indeterminate:checked + label {
  pointer-events: none;
  user-select: none;

  &::before {
    @apply --fade-in;
    height: 2px;
    left: 2px;
    transform: rotate(0);
    top: 10px;
    width: calc(16px - 6px);
  }

  &::after {
    background-color: var(--grey-600);
    border: 2px solid var(--grey-600);
  }
}

.c-checkbox--informative:checked + label {
  &::before {
    border-color: var(--blue-600);
  }
  &::after {
    background-color: transparent;
    border: 1px solid var(--blue-600);
  }
}

.c-checkbox--stacked {
  & + label {
    display: block;
    margin-bottom: 1em;
  }
}