/*---
title: Typography
section: Elements
---*/

:root {
  /* stylelint-disable root-no-standard-properties */
  color: var(--color-page);
  font: var(--fontSize-page) / var(--lineHeight-page) var(--fontFamily-page);
  font-weight: var(--fontWeight-page);
  text-rendering: optimizeLegibility;
  text-size-adjust: 100%; /* iOS 8+ */
  /* stylelint-enable */
}

::selection {
  background-color: var(--highlight-warning);
  color: var(--color-page);
  text-shadow: none;
}

:--headings {
  font-size: var(--fontSize-heading);
  font-weight: inherit;
  line-height: var(--lineHeight-heading);
  margin: 0 0 var(--frame-quarter);
}

:--code { font-family: var(--fontFamily--monospaced); }

:--anchor-default {
  color: var(--color-link);
  text-decoration: none;

  &:--enter { text-decoration: underline; }
}

hr {
  border-top: 2px solid var(--borderColor);
  margin: var(--frame-half) 0;
}

p {
  line-height: var(--lineHeight-paragraph);
  margin-bottom: var(--frame-half);
}

.is-featured p:first-of-type {
  font-size: var(--fontSize--semiMedium); 
}

small {
  display: block;
  font-size: var(--fontSize--tiny);
  line-height: var(--lineHeight-page);
  margin-bottom: var(--frame-half);
}

.is-positive { color: var(--color-positive) !important; }
.is-informative { color: var(--color-informative) !important; }
.is-neutral { color: var(--color-neutral) !important; }
.is-live { color: var(--color-live) !important; }
.is-alterable { color: var(--color-alterable) !important; }
.is-negative { color: var(--color-negative) !important; }
