/*---
title: Accordion
section: Objects
---*/

:root {
  --accordion-top-color: var(--blue-100);
  --accordion-mid-color: var(--blue-50);
  --accordion-bottom-color: var(--blue-50);
  --accordion-hover-color: var(--blue-800);
  --accordion-text-color: var(--grey-800);
  --accordion-toggle-color: var(--grey-500);
}

.o-accordion {
  @apply --reset-box;
  color: var(--accordion-text-color);
  position: relative;

  &.is-collapsed {
    width: 21px;

    .o-accordion__label {
      background-color: var(--accordion-mid-color);
      padding-left: 0;
      padding-right: 0;
      text-indent: -999999px;
      width: 21px;
    }

    .o-accordion__label *,
    .o-accordion__label::before,
    .o-accordion__item .o-accordion__item {
      @apply --fade-out;
      padding: 0;
      margin: 0;
      width: 21px;
    }
  }
}

.o-accordion__list { @apply --reset-box; }

.o-accordion__list--top {
  overflow-x: hidden;
  overflow-y: auto;
}

.o-accordion__label {
  background-color: var(--accordion-top-color);
  color: var(--accordion-text-color);
  display: block;
  font-size: var(--fontSize--tiny);
  font-weight: bold;
  margin-bottom: 0 !important;
  padding: .5em 1.5rem;
  position: relative;
  text-transform: uppercase;
  transition: background-color var(--global-duration) ease-out, color var(--global-duration) ease-out;
  user-select: none;

  &:hover,
  &:focus { text-decoration: none; }

  &::before {
    @apply --triangle;
    @apply --triangle-right;
    border-width: 5px 0 5px 8px;
    color: var(--grey-700);
    content: '';
    cursor: pointer;
    left: 10px;
    position: absolute;
    top: 10px;
    transition: transform var(--global-duration) ease-out, border var(--global-duration) ease-out;
  }
}

.o-accordion__item {
  cursor: pointer;
  font-size: var(--fontSize--small);
  position: relative;

  &.is-open {
    & > .o-accordion__list {
      @apply --fade-in;
      display: inherit;
    }

    & .o-accordion__label::before { transform: rotate(90deg); }
  }
}

.o-accordion__list--mid {
  @apply --fade-out;
  display: none;
  background-color: var(--accordion-mid-color);

  & .o-accordion__item {
    min-height: 50px;
    padding: .75em 1.25rem;

    &:not(:last-child) { border-bottom: 1px solid var(--grey-300); }

    &.is-active,
    &:hover,
    &:focus {
      background-color: var(--blue-100);
      box-shadow: inset 0 0 4px rgba(0, 0, 0, .5);

      & .o-accordion__action { @apply --fade-in; }
    }
  }
}

.o-accordion__list--bottom {
  @apply --fade-out;
  background-color: var(--accordion-bottom-color);
}

.o-accordion__title {
  @apply --ellipsis;
  font-size: var(--fontSize--tiny);
  font-weight: bold;
  line-height: 1.5;
  margin: 0;
  text-transform: uppercase;
  width: 14em;
}

.o-accordion__description {
  @apply --ellipsis;
  padding-bottom: 1px; /* Hack overflow for descenders */
}

.o-accordion__actions {
  position: absolute;
  right: 0;
  top: 1px;
}

.o-accordion__action {
  @apply --fade-out;
  background-color: var(--blue-600);
  color: contrast(var(--blue-600));
  cursor: pointer;
  display: block;
  float: left;
  height: 36px;
  overflow: hidden;
  transition: background-color var(--global-rounded) ease;
  width: 36px;

  &:hover {
    background-color: var(--blue-700);
  }

  &:focus {
    box-shadow: inset 0 0 4px rgba(0, 0, 0, .5);
    outline: none;
  }
}

.o-accordion__action--success {
  background-color: var(--green-600);
  color: contrast(var(--green-600));

  &:hover {
    background-color: var(--green-700);
  }
}

.o-accordion__toggle {
  background-color: var(--accordion-toggle-color);
  color: var(--white);
  display: block;
  height: 24px;
  position: relative;
  width: 100%;

  &:focus {
    outline: none;
  }

  &::before {
    @apply --triangle;
    @apply --triangle-left;
    border-width: 6px 7px 6px 0;
    content: '';
    position: absolute;
    right: 8px;
    top: 6px;
  }

  .is-collapsed &::before {
    @apply --triangle-right;
    border-width: 6px 0 6px 7px;
  }

  &::after {
    position: absolute;
    top: 7px;
  }

  .o-accordion--left {
    border-radius: 0 0 var(--global-rounded) 0;

    &::after {
      @apply --arrow-left;
      color: var(--white);
      content: '';
      height: 5px;
      right: 10px;
      width: 7.5px;
    }
  }
  
  .o-accordion--right {
    border-radius: 0 0 0 var(--global-rounded);

    &::after {
      @apply --arrow-right;
      color: var(--white);
      content: '';
      height: 5px;
      left: 10px;
      width: 7.5px;
    }
  }
}
