/*---
title: Buttons
section: Components
---*/

:root {
  --button-background: var(--blue-800);
  --button-background-hover: var(--blue-700);
  --button-background-hover-alert: var(--red-600);
  --button-background-hover-passive: var(--white);
  --button-background-hover-secondary: var(--orange-600);
  --button-background-hover-success: var(--green-600);
  --button-background-hover-text: var(--grey-100);
  --button-background-alert: var(--red-700);
  --button-background-anchor: transparent;
  --button-background-passive: var(--blue-200);
  --button-background-secondary: var(--orange-700);
  --button-background-success: var(--green-700);
  --button-background-text: transparent;
  --button-border: 1px solid var(--grey-600);
  --button-text-color: var(--white);
  --button-text-color-anchor: var(--blue-700);
  --button-text-color-anchor-active: var(--blue-800);
  --button-text-color-passive: var(--grey-800);
  --button-text-color-text: currentColor;
  --button-hover-shadow: 0 0 6px var(--shadow-color);
  --button-duration: .1s;
  --button-disabled-opacity: .35;
  --button-large: 36px;
  --button-regular: 30px;
  --button-xlarge: 68px;
}

.c-button {
  appearance: button;
  background-color: var(--button-background);
  border-radius: var(--global-rounded);
  border-width: 0;
  color: var(--button-text-color);
  cursor: pointer;
  display: inline-block;
  font-family: inherit;
  font-size: var(--font-size--small);
  height: var(--button-regular);
  line-height: var(--button-regular);
  margin: 0 var(--frame-half) var(--frame-half) 0;
  max-width: 100%;
  min-width: 24px;
  outline: 0;
  overflow: visible; /* Internet Explorer 12- */
  padding: 0 var(--frame-quarter);
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: background var(--button-duration) ease, color var(--button-duration) ease;
  user-select: none;
  vertical-align: middle;
  white-space: nowrap;

  &:--enter {
    & .u-fade-out { @apply --fade-in; }
  }

  &:focus { outline: 0; }

  &:--disabled {
    opacity: var(--button-disabled-opacity);
    -ms-pointer-events: none;
    pointer-events: none;
  }

  &:hover::after { opacity: 1; }

  &:--active {
    background-color: var(--button-background-hover);

    &::after { opacity: 0 !important; }
  }

  &::after {
    border-radius: inherit;
    box-shadow: var(--button-hover-shadow);
    bottom: 0;
    content: '';
    left: 0;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: opacity var(--button-duration) ease;
    z-index: 5;
  }

  &::-moz-focus-inner {
    border: 0 !important;
    padding: 0 !important;
  }


  &:last-child { margin-right: 0 !important; }
  
  &__content { display: inline-block; }

  .c-icon {
    height: 20px;
    margin-top: -3px;
    width: 20px;

    &:first-child { margin-right: 7px; }

    &:last-child { margin-left: 7px; }
  }

  .c-badge {
    background-color: var(--white);
    color: var(--button-background);
    margin-bottom: 3px;
  }
}

.c-button--wide {
  display: block;
  width: 100%;
}

.c-button--round { border-radius: 50px !important; }

.c-button--large {
  font-size: var(--font-size--normal) !important;
  height: var(--button-large);
  line-height: var(--button-large);
}

.c-button--xlarge {
  @apply --middle;
  display: flex;
  font-size: var(--font-size--normal-plus) !important;
  height: var(--button-xlarge);
  line-height: var(--button-xlarge);
  padding: 0 var(--frame-half);

  .c-button__content {
    @apply --ellipsis;
    @apply --start;
  }
}

.c-button--icon {
  background-color: transparent;
  color: var(--grey-700);
  display: inline-block;
  height: auto;
  margin: 0;
  min-width: auto;

  &:hover { color: var(--grey-800); }

  &:--active {
    background-color: transparent;
    color: var(--button-background-hover);
  }

  &::after { opacity: 0 !important; }

  .c-icon {
    height: inherit;
    margin: 0 !important;
    width: inherit;
  }
}

.c-button--postfix {
  border-radius: 0 var(--global-rounded) var(--global-rounded) 0;
  margin-left: -4px;
}

.c-button--postinset {
  background-color: transparent;
  margin: 0;
  position: absolute;
  right: 0;
  top: 0;
}

.c-button--dropdown {
  padding-right: 23px;
  &::before {
    @apply --triangle;
    @apply --triangle-bottom;
    border-width: 5px 4px 0;
    content: '';
    position: absolute;
    right: 8px;
    top: 44%;
  }
}

.c-button--success {
  background-color: var(--button-background-success);
  &:--active { background-color: var(--button-background-hover-success); }
  .c-badge { color: var(--button-background-success); }
}

.c-button--secondary {
  background-color: var(--button-background-secondary);
  &:--active { background-color: var(--button-background-hover-secondary); }
  .c-badge { color: var(--button-background-secondary); }
}

.c-button--alert {
  background-color: var(--button-background-alert);
  &:--active { background-color: var(--button-background-hover-alert); }
  .c-badge { color: var(--button-background-alert); }
}

.c-button--passive {
  background-color: var(--button-background-passive);
  border: var(--button-border);
  color: var(--button-text-color-passive);
  &:--active { background-color: var(--button-background-hover-passive); }
  .c-badge { color: var(--button-background-passive); }
}

.c-button--text {
  background-color: var(--button-background-text);
  border: 1px solid transparent;
  color: var(--button-text-color-text);
  &:--active {
    background-color: var(--button-background-hover-text);
    border: var(--button-border);
  }
  .c-badge { color: var(--button-background-text); }
}

.c-button--anchor {
  background-color: var(--button-background-anchor);
  box-shadow: none;
  color: var(--button-text-color-anchor);
  text-transform: uppercase;
  &:--active {
    background-color: var(--button-background-anchor);
    color: var(--button-text-color-anchor-active);
    text-decoration: underline !important;
  }
  .c-badge { color: var(--button-background-anchor); }
}
