/*---
title: Typography
section: Utilities
---*/

:root {
  --fontFamily--serif: 'Source Sans Pro', sans-serif;
  --fontFamily--monospaced: Consolas, "Andale Mono WT", "Andale Mono", "Lucida Console", "Lucida Sans Typewriter", "DejaVu Sans Mono", "Bitstream Vera Sans Mono", "Liberation Mono", "Nimbus Mono L", Monaco, "Courier New", Courier, monospace;
  --fontFamily-page: var(--fontFamily--serif);
  --fontSize--tiny: .75rem; /* 12px */
  --fontSize--small: .875rem; /* 14px */
  --fontSize--semiNormal: 1rem; /* 16px */
  --fontSize--normal: 1.125rem; /* 18px */
  --fontSize--normalPlus: 1.25rem; /* 20px */
  --fontSize--semiMedium: 1.5rem; /* 24px */
  --fontSize--medium: 2rem; /* 32px */
  --fontSize--semiLarge: 2.5rem; /* 40px */
  --fontSize--large: 3rem; /* 48px */
  --fontSize--xlarge: 4.7rem; /* 75.2px */
  --fontSize-page: 100%;
  --fontSize-global: var(--fontSize--semiNormal);
  --fontSize-heading: var(--fontSize--normalPlus);
  --fontWeight-page: 400;
  --fontWeight-page--bold: 800;
  --lineHeight-page: 1;
  --lineHeight-paragraph: 1.4;
  --lineHeight-heading: 1.3;

  /* DEPRECATED Variables */
  --font-family: 'Source Sans Pro', sans-serif;
  --font-size: 100%;
  --font-size--tiny: .75rem; /* 12px */
  --font-size--small: .875rem; /* 14px */
  --font-size--semi-normal: 1rem; /* 16px */
  --font-size--normal: 1.125rem; /* 18px */
  --font-size--normal-plus: 1.25rem; /* 20px */
  --font-size--semi-medium: 1.5rem; /* 24px */
  --font-size--medium: 2rem; /* 32px */
  --font-size--semi-large: 2.5rem; /* 40px */
  --font-size--large: 3rem; /* 48px */
  --font-size--xlarge: 4.7rem; /* 75.2px */
  --font-line-height: var(--lineHeight-page);
  --font-weight: 400;
  --font-weight--bold: 800;
  --global-font-size: var(--font-size--semi-normal);
  --heading-font-size: var(--font-size--normal-plus);
  /* End Deprecation */

  --no-bullet: {
    list-style-image: none;
    list-style-type: none;
    margin-left: 0;
  };
  --list-horizontal: {
    border: 0;
    clear: both;
    margin: 0;
    padding: 0;
  };
  --ellipsis: {
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
  };
  --break-word: {
    -ms-word-break: break-all; /* Safari can wait */
    word-break: break-word;
    word-wrap: break-word;
  };
}

.u-no-bullet { @apply --no-bullet; }
.u-no-bullets {
  list-style: none;
  &__item { @apply --no-bullet; };
}
.u-list-horizontal { @apply --list-horizontal; }
.u-ellipsis { @apply --ellipsis; }
.u-break-word { @apply --break-word; }