/*---
title: Button Groups
section: Objects
---*/

.o-button-group {
  box-sizing: border-box;
  display: inline-block;
  margin: 0 0 2%;
  padding: 0;

  .c-button {
    margin-right: -2px !important;
    margin-bottom: 2px !important;

    &:first-child {
      border-bottom-right-radius: 0 !important;
      border-top-right-radius: 0 !important;
    }

    &:not(:first-child):not(:last-child) { border-radius: 0 !important; }

    &:not(:first-child) {
      margin-left: 0 !important;
    }

    &:last-child {
      border-bottom-left-radius: 0 !important;
      border-top-left-radius: 0 !important;
    }
  }

  .c-button--postfix {
    border-radius: 0;

    &:last-child { border-radius: 0 var(--global-rounded) var(--global-rounded) 0; }
  }
}

.o-button-group__label {
  display: inline-block;
  margin: 0 .5rem 0 !important;
  line-height: 30px !important;
}

.o-button-group--stack {
  clear: both;
  list-style: none;

  .c-button {
    margin: 0 0 1px 0 !important;
    width: 100%;
  }
}

.o-button-group--toggle {
  @apply --text-middle;
  border: 1px solid currentColor;
  border-radius: var(--global-rounded);
  box-sizing: border-box;
  display: inline-flex;
  flex: 0 1 auto;
  flex-flow: row wrap;
  height: 30px;
  line-height: 30px;
  margin-bottom: .5em;
  margin-right: 2%;
  overflow: hidden;

  @media (--md-up) { margin-bottom: 0; }
  
  .c-button {
    border: none;
    height: 26px;
    line-height: 26px;
    margin: 1px !important;
    min-width: 50px;
    transition: color var(--global-duration) ease, background-color var(--global-duration) ease !important;

    &:--enter { border: none; }

    &::after { box-shadow: none; }
  }

  .c-toolbar & { margin-bottom: 0 !important; }
}
