/*---
title: Toolbar
section: Objects
---*/

:root {
  --brand-logo: 'img/navis-logo--inverted.svg';
  --toolbar-height: calc(56px + 1em);
}

.o-toolbar {
  @apply --row;
  @apply --middle;
  background-color: var(--color-primary);
  border-color: var(--bar-border-color);
  border-style: solid;
  border-width: 0 0 1px;
  color: var(--grey-500);
  height: 56px;
  padding: 0 .5rem;

  & .c-button--icon {
    color: var(--white);
    &:hover { color: var(--white); }
    &:active,
    &.is-active { color: var(--nav-active-color); }
  }

  & .c-icon { color: var(--white); }

  & * { margin-bottom: 0 !important; }

  & .u-column--bar {
    &:nth-child(even) { @apply --end; }

    &:nth-child(odd) { @apply --start; }
  }
}

.o-toolbar__brand {
  @apply --hide-text;
  /* stylelint-disable function-url-quotes */
  background-image: url(var(--brand-logo));
  /* stylelint-enable */
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  height: 48px;
  margin: 6px 0 0 !important;
  position: relative;
  width: 80px;
  @media (--md-up) {
    width: 94px;
  }
}

.o-toolbar__brand,
.o-toolbar__product {
  display: inline-block;
  color: var(--white);
  text-transform: uppercase;
}

.o-toolbar__link {
  display: inline-block;
  padding: 0 10px;
  position: relative;
  vertical-align: middle;
}

.o-toolbar--small {
  border-bottom-width: 3px !important;
  height: 44px;
}

.o-toolbar--secondary {
  border: none !important;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 200;
  @media (--md-up) {
    background-color: var(--white);
    box-shadow: var(--shadow-1);
    color: var(--color-page);
    top: 100px;
    z-index: 20;

    & .c-icon { color: currentColor; }

    & .u-column {
      @apply --middle;
      display: flex;
    }
  }
}

@media (--sm-down) {
  .o-toolbar__view {
    @apply --width-column-6;
    h1 {
      display: inline-block;
      font-size: var(--fontSize--semiMedium);
      font-weight: normal;
      margin: 0;
      vertical-align: middle;
      white-space: nowrap;
    }
  }

  .o-toolbar__action {
    display: inline-block;
    padding: 1em;
    margin: 0;
  }

  .o-toolbar__actions {
    @apply --width-column-6;
    @apply --end;
    > div,
    > ul,
    > li { display: inline-block; }
  }
}
