/*---
title: Messages
section: Components
---*/

:root {
  --color-message: var(--color-informative);
  --fontSize-message: var(--fontSize--tiny);
  --message: {
    color: var(--color-message);
    font-size: var(--fontSize-message);
  };
}

.c-message { @apply --message; }

.c-message--success {
  --color-message: var(--color-positive);
  @apply --message;
}

.c-message--neutral {
  --color-message: var(--color-neutral);
  @apply --message;
}

.c-message--warning {
  --color-message: var(--color-live);
  @apply --message;
}

.c-message--alert {
  --color-message: var(--color-negative);
  @apply --message;
}
