/*---
title: Positioning
section: Utilities
---*/

:root {
  --position-bottom: {
    bottom: 0;
    left: 0;
    right: 0;
  };
  --position-bottom-left: {
    bottom: 0;
    left: 0;
  };
  --position-bottom-right: {
    bottom: 0;
    right: 0;
  };
  --position-middle: {
    bottom: auto;
    left: 50%;
    right: auto;
    top: 50%;
    transform: translate(-50%, -50%);

    *,
    *::before,
    *::after {
      transition: none !important; /* Bug with font-smoothing vs translate */
    }
  };
  --position-top: {
    left: 0;
    right: 0;
    top: 0;
  };
  --position-top-left: {
    left: 0;
    top: 0;
  };
  --position-top-right: {
    right: 0;
    top: 0;
  };
  --position-full: {
    bottom: 0;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    width: 100%;
  };
}

.u-fixed--top-center {
  @apply --position-top;
  position: fixed !important;
  width: calc(100% - 1em);
}
.u-fixed--top {
  @apply --position-top;
  position: fixed !important;
}
.u-fixed--top-right {
  @apply --position-top-right;
  position: fixed !important;
}
.u-fixed--top-left {
  @apply --position-top-left;
  position: fixed !important;
}
.u-fixed--bottom {
  @apply --position-bottom;
  position: fixed !important;
}
.u-fixed--bottom-right {
  @apply --position-bottom-right;
  position: fixed !important;
}
.u-fixed--bottom-left {
  @apply --position-bottom-left;
  position: fixed !important;
}
.u-fixed--middle {
  @apply --position-middle;
  position: fixed !important;
}
.u-fixed--middle-center {
  @apply --position-middle;
  @apply --center;
  position: fixed !important;
}
.u-absolute--top {
  @apply --position-top;
  position: absolute !important;
}
.u-absolute--top-right {
  @apply --position-top-right;
  position: absolute !important;
}
.u-absolute--top-left {
  @apply --position-top-left;
  position: absolute !important;
}
.u-absolute--bottom {
  @apply --position-bottom
  position: absolute !important;
}
.u-absolute--bottom-right {
  @apply position-bottom-right;
  position: absolute !important;
}
.u-absolute--bottom-left {
  @apply --position-bottom-left;
  position: absolute !important;
}
.u-absolute--middle {
  @apply --position-middle;
  position: absolute !important;
}
.u-absolute--middle-center {
  @apply --position-middle;
  @apply --center;
  position: absolute !important;
}
