/*---
title: Notification
section: Components
---*/

:root {
  --notification-margin: calc(var(--margin-outter) - .5rem);
  --notification-width: 400px;
  --notification-color: blue;
  --notification-success-color: green;
  --notification-warning-color: gold;
  --notification-error-color: gold;
}

.c-notification {
  @apply --card;
  box-shadow: var(--shadow-2);
  padding: .5em 2.5rem .5em .5rem !important;
  position: relative;
  text-align: left;
  transition-duration: var(--global-duration);
  @media (--md-up) {
    margin: 0 auto;
    width: var(--notification-width);
  }

  & > .c-icon {
    color: var(--blue-700);
    left: 16px;
    position: absolute;
    top: 12px;
    @media (--md-up) {
      font-size: var(--fontSize--semiMedium);
      margin-top: .25rem;
    }
  }

  .c-button--icon {
    padding: 0;
    position: absolute;
    right: 12px;
    top: 12px;
  }

  &.u-fixed--top { border-radius: 0 0 var(--global-rounded) var(--global-rounded); }
  &.u-fixed--bottom { border-radius: var(--global-rounded) var(--global-rounded) 0 0; }
}

.c-notification__heading {
  font-size: var(--fontSize--small);
  font-weight: bold;
  line-height: 1.3;
  margin: 7px 0 0;
}

.c-notification__content {
  display: inline-block;
  font-size: var(--fontSize--small);
  left: 40px;
  position: relative;
  width: 87%
}

.c-notification--wide {
  @apply --row;
  @apply --middle;
  min-height: 48px;

  @media (--md-up) { width: auto; }

  & > .c-icon {
    @media (--md-up) { margin-top: 0; }
  }

  .c-notification__heading,
  .c-notification__content p {
    display: inline;
  }

  .c-button--icon {
    opacity: .6;
    top: auto;
  }
}

.c-notification--success {
  & > .c-icon { color: var(--green-700); }
}

.c-notification--warning {
  background-color: var(--gold-50);
  & > .c-icon { color: var(--orange-600); }
}

.c-notification--error {
  background-color: var(--gold-50);
  & > .c-icon { color: var(--red-700); }
}

.c-notification--informative {
  background-color: var(--blue-600);
  color: var(--white);
  & > .c-icon,
  .c-button--icon { color: var(--white); }
}