/*---
title: Radio
section: Components
---*/

.c-radio { @apply --hide-a11y; }

.c-radio:checked + label {
  &::before {
    background-color: var(--green-600);
    border: 2px solid var(--green-600);
    color: #99a1a7;
  }
  &::after { @apply --fade-in; }
}

.c-radio + label {
  cursor: pointer;
  display: inline-block;
  margin-right: 5px;
  position: relative;

  &::before {
    background-color: var(--white);
    border: 2px solid var(--grey-700);
    border-radius: 50px;
    content: '';
    display: inline-block;
    float: left;
    font-size: var(--fontSize--small);
    margin-bottom: 0;
    margin-right: 8px;
    padding: 6px;
    position: relative;
    top: 3px;
  }

  &::after {
    @apply --fade-out;
    background-color: var(--white);
    border-radius: 50px;
    content: '';
    height: 6px;
    left: 5px;
    position: absolute;
    top: 8px;
    width: 6px;
    z-index: 5px;
  }
}

.c-radio:focus + label,
.c-radio + label:active,
.c-radio:checked + label:active {
  box-shadow: var(--toggle-focus-shadow);
}

/* Alternate Blue */

.c-radio--informative + label {
  &::before {
    background-color: transparent;
    border: 1px solid var(--grey-700);
    height: 20px;
    top: 0;
    width: 20px;
  }

  &::after {
    background-color: var(--blue-600);
    left: 7px;
    top: 7px;
  }
}

.c-radio--informative:checked + label {
  &::before {
    background-color: transparent;
    border: 1px solid var(--blue-600);
  }
}

/* Stacked Orientation */

.c-radio--stacked {
  & + label {
    text-align: center;
    width: 20px;
  }

  & + label::before {
    margin-bottom: .5em;
  }
}