/*---
title: Syntax
section: Utilities
---*/

/* Custom Element Selectors */
@custom-selector :--anchor-default a[href]:not([class]);
@custom-selector :--app-blocks .s-app, .s-app__containers, .s-app__body;
@custom-selector :--blocks article, aside, figcaption, figure, footer, header, hgroup, main, menu, nav, section;
@custom-selector :--button button, .c-button, input[type='submit'];
@custom-selector :--clickable a, area, button, input, label, select, textarea, [tabindex];
@custom-selector :--code code, kbd, pre, samp;
@custom-selector :--control input, select, textarea;
@custom-selector :--control-text [type="button"], [type="date"], [type="datetime"], [type="datetime-local"], [type="email"], [type="month"], [type="number"], [type="password"], [type="reset"], [type="search"], [type="submit"], [type="tel"], [type="text"], [type="time"], [type="url"], [type="week"], select, textarea;
@custom-selector :--control-button button, [type="button"], [type="reset"], [type="submit"];
@custom-selector :--headings h1, h2, h3, h4, h5, h6;
@custom-selector :--lists ul, ol, dl;
@custom-selector :--list-items li, dt, dd;
@custom-selector :--media audio, canvas, iframe, img, svg, video;

/* Custom Pseudo Class Selectors */
@custom-selector :--active :active, .is-active;
@custom-selector :--any-link :link, :visited;
@custom-selector :--disabled [disabled], .is-disabled;
@custom-selector :--enter :hover, :focus, .is-hover;
@custom-selector :--invalid :invalid, .is-invalid;

.s-healthcheck {
  [class] { outline: 5px solid lightgrey; }

  [class*='__'] { outline: 5px solid grey; }

  [class*='--'] { outline: 5px solid darkgrey; }

  [class^='o-'],
  [class*=' o-'] { outline: 5px solid orange; }

  [class^='c-'],
  [class*=' c-'] { outline: 5px solid cyan; }

  [class*='@'] { outline: 5px solid rosybrown; }

  [class^='_'] { outline: 5px solid red; }
}
