/*---
title: Forms
section: Elements
---*/

:root {
  --form-background: var(--white);
  --form-border: 1px solid var(--grey-600);
  --form-font-size: var(--font-size--small);
}

*:focus { outline: 0; }

:--control {
  background-color: var(--form-background);
  border: var(--form-border);
  border-radius: var(--global-rounded);
  color: inherit;
  display: flex;
  font-family: inherit;
  font-size: var(--form-font-size);
  margin-bottom: 16px;
  min-height: 30px; /* IE11 Input Height */
  padding-left: 4px;
  position: relative;
  width: 100%;

  &::-ms-value {
    color: currentColor; /* Edge 12+, Internet Explorer 11- */
  }
}

select::-ms-expand {
  display: none !important; /* Edge 12+, Internet Explorer 11- */
}

label {
  display: inline-block;
  font-size: var(--form-font-size);
  font-weight: 600;
  line-height: 1.5;
  margin-bottom: 5px;
  max-width: 100%;

  &.is-optional { font-weight: normal; }
}
