/*---
title: Menu
section: Components
---*/

:root {
  --menu-active-color: var(--nav-active-color);
  --menu-active-copy: var(--nav-active-copy);
  --menu-item-height: 41px;
}

.c-menu {
  @apply --list-horizontal;
  @apply --row;
  background-color: transparent;
  color: currentColor;
  list-style: none;
  position: relative;
  text-decoration: none;
  user-select: none;
  vertical-align: middle;

  &__item { @apply --no-bullet; }
}

.c-menu__link {
  background-color: transparent;
  color: currentColor;
  cursor: pointer;
  font-family: inherit;
  padding: 0 .5rem;
  @media (--md-up) {
    min-width: 100px;
  }

  &.is-active {
    background-color: var(--blue-800);
    box-shadow: inset 0 0 6px rgba(0, 0, 0, .8);
    color: var(--menu-active-copy);
  }
}

.c-menu__link--small {
  font-size: var(--fontSize--small);
  height: 38px;
  line-height: 38px;
}

.c-menu--block {
  margin: 0 -.5rem;

  .c-menu__link {
    display: block;
    font-size: var(--fontSize--tiny);
    height: var(--menu-item-height);
    line-height: var(--menu-item-height);
    padding: 0 12px;
    transition: background-color var(--global-duration) ease, color var(--global-duration) ease;
    white-space: nowrap;
    width: 100%;
    @media (--md-up) {
      font-size: var(--fontSize--normal);
      padding: 0 24px;
    }

    &:hover,
    &:active,
    &.is-active {
      background-color: var(--menu-active-color);
      box-shadow: none;
      color: var(--menu-active-copy);
    }

    &.is-active + .c-menu__submenu {
      @apply --fade-in;
    }
  }

  .c-menu__submenu {
    @apply --fade-out;
    @apply --reset-box;
    color: currentColor;
    display: block;
    list-style: none;
    min-width: 150px;

    &.is-active { @apply --fade-in; }

    &__item { @apply --no-bullet; }
  }
}

.c-menu--inline {

  .c-menu__link {
    border-bottom: 3px solid transparent;
    font-size: var(--fontSize--tiny);
    letter-spacing: 1px;
    min-height: 36px;
    padding: .5em .5rem;
    text-transform: uppercase;
    transition: border-bottom var(--global-duration) ease, color var(--global-duration) ease;

    @media (--md-up) {
      min-width: auto;
      padding: .5em .75rem;
    }

    &:hover { border-bottom-color: var(--blue-400); }

    &:active,
    &.is-active {
      background-color: transparent;
      border-bottom-color: var(--blue-800);
      box-shadow: none;
      color: var(--blue-800);
    }
  }
}
