/*---
title: Label
section: Components
---*/

:root {
  --fontSize-label: var(--fontSize--tiny);
  --padding-label: 2px 3px 1px;
  --margin-label: 0 18px 6px 0;
  --size-label-icon: 15px;
  --offset-label-icon: -3px;
  --label-inline: {
    display: inline-block;
    font-size: var(--fontSize-label);
    line-height: 1;
    margin: var(--margin-label);
    max-width: 100%;
    outline: 0;
    padding: var(--padding-label);
    position: relative;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    white-space: nowrap;
  };
}

.c-label {
  @apply --label-inline;
  background-color: var(--color-label-background);
  border-radius: calc(var(--global-rounded) / 2);
  color: var(--color-label);
  text-transform: uppercase;
  user-select: none;

  & .c-icon {
    fill: var(--color-label-icon);
    height: var(--size-label-icon);
    margin-top: var(--offset-label-icon);
    width: var(--size-label-icon);
  }
}

/* DEPRECATED
 * Spectrum-based color classes
 * Labels should be unopinionated to content and culture. */

.c-label--green,
.c-label--positive {
  background-color: var(--color-label-green-background);
  color: var(--white);
}
.c-label--blue,
.c-label--informative { background-color: var(--color-label-blue-background); }
.c-label--magenta,
.c-label--informative-alt {
  background-color: var(--color-label-magenta-background);
  color: var(--white);
}
.c-label--grey,
.c-label--neutral { background-color: var(--color-label-grey-background); }
.c-label--gold,
.c-label--live { background-color: var(--color-label-gold-background); }
.c-label--orange,
.c-label--alterable { background-color: var(--color-label-orange-background); }
.c-label--red,
.c-label--negative {
  background-color: var(--color-label-red-background);
  color: var(--white);
}
