/*---
title: Loader
section: Components
---*/

.c-loader {
  animation: spin 3s alternate infinite;
	position: relative;
  transform: rotate(20deg);
  width: 30px;

  &::before {
    color: var(--color-secondary);
  }

  &::after {
    color: var(--grey-300);
    transform: rotate(180deg);
  }

  &::before,
  &::after {
    @apply --triangle;
    @apply --triangle-top;
    content: '';
    border-bottom-width: 2em;
  }
}

@keyframes spin {
  0% { transform: scale(1) rotate(0deg); }
	50% { transform: scale(1) rotate(80deg); }
	100% { transform: scale(1) rotate(-160deg); }
}
