/*---
title: Modal
section: Objects
---*/

:root {
  --backgroundColor-modal: var(--grey-100);
  --backgroundColor-modal__title: var(--color-primary);
}

.o-modal {
  @apply --fade-out;
  @apply --row;
  @apply --start;
  position: fixed;
  z-index: 500;

  @media (--md-up) { position: static; }

  &:--active {
    @apply --fade-in;
    z-index: 300;
  }
}

.o-modal__backdrop {
  @apply --fade-out;
  @apply --position-full;
  background: rgba(0, 0, 0, .6);
  position: fixed;
  z-index: 400 !important;
  @media (--md-up) {
    :--active &,
    :--active { @apply --fade-in; }
  }
}

.o-modal__card {
  @apply --card;
  @apply --position-top;
  background-color: var(--backgroundColor-modal);
  bottom: 56px;
  box-shadow: none;
  margin-bottom: 0;
  padding: 0;
  position: fixed;
  z-index: 450;
  @media (--md-up) {
    @apply --position-middle;
    box-shadow: var(--shadow-1);
    margin-bottom: 1em;
    min-height: 120px;
    min-width: 370px;
  }
}

.o-modal__title {
  background-color: var(--backgroundColor-modal__title);
  color: var(--white);
  font-size: var(--fontSize--semiMedium);
  margin-bottom: .5em;
  margin-top: -1px;
  padding: .5em 1rem;
  position: relative;
}

.o-modal__close {
  background-color: transparent;
  color: rgba(255, 255, 255, .5);
  cursor: pointer;
  display: none;
  position: absolute;
  right: 5px;
  top: 5px;
  @media (--md-up) { display: inline; }

  .c-icon {
    fill: rgba(255, 255, 255, .5);
    height: 40px;
    width: 40px;
  }
}

.o-modal__back {
  background-color: transparent;
  cursor: pointer;
  display: inline;

  @media (--md-up) { display: none; }

  .c-icon {
    fill: rgba(255, 255, 255, .5);
    height: 40px;
    width: 40px;
  }
}

.o-modal__content {
  padding: .5em 1rem;
  > div:not([class]) { @apply --width-column-12; }
}

.o-modal__actions {
  @apply --width-column-12;
  @apply --end;
}

.o-modal--small,
.o-modal--help {
  --backgroundColor-modal__titlebar: var(--blue-700);
  --backgroundColor-modal: var(--white);

  & .o-modal__title {
    @apply --row;
    @apply --middle;
    background-color: var(--backgroundColor-modal__titlebar);
    border-radius: 4px 4px 0 0;

    .c-icon:first-child { margin-right: 5px; }

    .o-modal__close .c-icon {
      height: 30px !important;
      top: .4em;
      width: 30px !important;
    }
  }

  & :--headings {
    display: inline-block;
    margin: 5px 0 !important;
  }

  & .o-modal__card { border-radius: 4px; }
}

.o-modal__card {
  .o-modal--success &,
  .o-modal--primary & {
    color: var(--white);
    padding: .5em 1rem 1em;
  }

  .o-modal--success & {
    background-color: var(--green-800);
  }

  .o-modal--primary & {
    background-color: var(--blue-800);
  }

  .o-modal--warning & {
      background-color: var(--gold-700);
      padding: .5em 1rem 1em;
  }
}
