/*---
title: Card
section: Components
---*/

:root {
  --card: {
    background-color: var(--white);
    box-shadow: var(--shadow-1);
    color: var(--color-page);
    display: block;
    margin: 0 0 1em;
    max-width: 100%;
    min-width: 100px;
    overflow: hidden;
    padding: .5em 1rem;
    position: relative;
    transition: box-shadow var(--global-duration) ease;
    z-index: 1;
  };
  --card-wide: {
    background-color: var(--white);
    box-shadow: var(--shadow-1);
    color: var(--color-page);
    margin: 0 0 1em;
    overflow: hidden;
    padding: .5em 1rem;
    position: relative;
    z-index: 1;
  }
}
.c-card {
  @apply --card;

  &.is-active {
    box-shadow: var(--shadow-3) !important;
    z-index: 2;
  }

  &.is-inactive {
    border: 1px solid var(--borderColor-inactive);
    box-shadow: none;
  }
}

.c-card__title {
  @apply --row;
  @applw --middle;
  background-color: var(--grey-100);
  margin: -.5em -1rem .5em;
  min-height: 38px;
  padding: .5em 1rem;

  .is-inactive & { background-color: var(--white); }
}

.c-card__heading {
  display: inline-block;
  font-weight: bold;
  margin: 0;
}

.c-card--row {
  @apply middle;
  margin-bottom: 0;
  margin-left: -.5rem;
  margin-right: -.5rem;
  padding: .75em;
  position: relative;
  transition: background .1s ease-in-out, box-shadow .1s ease-in-out;

  &:not(:last-child) {
    border-bottom: 1px solid rgba(0, 0, 0, .1);
  }

  @media (--md-up) {
    margin-left: inherit;
    margin-right: inherit;
  }

  &:--active {

    .u-row {
      @apply --middle;
      &:not(:only-child) {
        margin: 0 0 .75em;
      }
      &:not(:first-child) {
        border-top: 1px solid var(--grey-100);
        margin-bottom: 0;
        padding-top: .75em;
      }
    }
  }

  &:focus .c-card__actions { @apply --fade-in; }

  @media (--md-up) {
    &:hover .c-card__actions { @apply --fade-in; }
  }
}

.c-card__actions {
  @apply --fade-out;
  color: var(--grey-700);

  :--active & { @apply --fade-in; }

  .c-icon {
    cursor: pointer;
    margin-left: .75em;
  }
}

.c-card--transparent {
  background: transparent !important;
  box-shadow: none !important;
}

.c-card--round { border-radius: var(--global-rounded); }

.c-card--zone {
  background-color: var(--blue-50);
  border: 1px dashed var(--blue-600);
  border-radius: var(--global-rounded);
  box-shadow: none;
  color: var(--blue-600);
  display: block;
  font-size: var(--font-size--normal);
  padding: 1em 1rem;
  text-align: center;
  text-transform: uppercase;
}

.c-card--small { padding: 0; }