/*---
title: Layout
section: Elements
---*/


/* Global Styles */

:root {
  --header-height: 56px;
  --mobile-header-height: var(--header-height);
  --sidenav-width: 12.25em;
  --sidenav-width-collapsed: 20px;
  /* stylelint-disable root-no-standard-properties */
  background-color: var(--backgroundColor-page);
  box-sizing: border-box;
  cursor: default;
  margin: 0;
  -ms-overflow-style: -ms-autohiding-scrollbar; /* Edge 12+, Internet Explorer 11- */
  overflow-x: hidden;
  overflow-y: scroll; /* All browsers without overlaying scrollbars */
  width: 100%;
  /* stylelint-enable */
}

::before,
::after {
  @apply --reset-text-border;
  text-decoration: inherit;
  vertical-align: inherit;
}

* {
  @apply --reset-text-border;
  background-repeat: no-repeat;
  font-size: inherit;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

/* Application Scope */

:--app-blocks {
  height: calc(100vh - var(--mobile-header-height));
  overflow-x: hidden;
  @media (--md-up) { height: calc(100vh - var(--header-height)); }
}

.s-app { overflow-y: hidden; }

.s-app__body {
  @apply --flex-layout;
  margin-top: var(--mobile-header-height);
  @media (--md-up) {
    margin-top: var(--header-height); /* combined height of .o-header */
  }
}

.o-header {
  @apply --position-top;
  position: fixed;
  z-index: 200;
}

.o-main {
  @apply --container-fluid;
  @apply --row;
  @apply --row-break;
  flex-direction: column;
  position: relative;
  @media (--md-up) { flex-direction: row; }
}

.o-nav {
  background-color: var(--sidenav-color);
  box-shadow: var(--sidenav-shadow);
  flex: 1 6 20%;
  order: 1;
  overflow-x: hidden;
  overflow-y: scroll;
  padding-right: 0;
  @media (--sm-down) { order: 0 !important; }
  @media (--md-up) {
    height: 100%;
    position: fixed;
    width: var(--sidenav-width);
    z-index: 10;
  }

  & .c-button,
  & .c-input {
    margin-bottom: 0 !important;
  }

  &.is-collapsed {
    width: var(--sidenav-width-collapsed);
  }

  &.has-search {
    padding-top: 30px; /* Input Height */

    .u-wrapper {
      position: fixed;
      top: 100px;
      width: 15em;
      z-index: 21;

      .c-input {
        border-radius: 0;
      }
    }
  }
}

.o-article {
  flex: 2 1 100%;
  order: 2;
  position: relative;
  @media (--sm-down) { order: 0 !important; }

  .is-collapsed + & {
    @media (--md-up) {
      margin-left: var(--sidenav-width-collapsed);

      .o-toolbar--secondary { margin-left: var(--sidenav-width-collapsed); }
    }
  }

  .o-nav:not(.is-collapsed) + & {
    flex: 2 1 80%;
    @media (--md-up) {
      margin-left: var(--sidenav-width);

      .o-toolbar--secondary { margin-left: var(--sidenav-width); }
    }
  }
}

.o-article__panel {
  margin: 0 auto;
  max-width: 970px;
}

.has-toolbar {
  margin-top: 56px;
  padding-top: 1em;
}

/* Email Layout */

.s-email__view,
.o-email-preheader {
  background-color: var(--backgroundColor-page);
}

.s-email__body {
  background-color: var(--white);
}