@custom-media --sm only screen and (min-width: 34em) and (max-width: 48em);
@custom-media --sm-up only screen and (min-width: 34em);
@custom-media --md only screen and (min-width: 48em) and (max-width: 62em);
@custom-media --md-up only screen and (min-width: 48em);
@custom-media --lg only screen and (min-width: 62em) and (max-width: 75em);
@custom-media --lg-up only screen and (min-width: 62em);
@custom-media --xl only screen and (min-width: 75em) and (max-width: 90em);
@custom-media --xl-up only screen and (min-width: 75em);
@custom-media --landscape only screen and (orientation: landscape);
@custom-media --portrait only screen and (orientation: portrait);

/* DEPRECATED Media Queries using max-width */
@custom-media --tn only screen and (max-width: 34em);
@custom-media --sm-down only screen and (max-width: 48em);
@custom-media --md-down only screen and (max-width: 62em);
