:root {
  --margin-outter: 1rem;
  --minWidth-media--small: 48;
  --minWidth-media--medium: 64;
  --minWidth-media--large: 75;
  --width-gutter: 1rem;
  --width-gutter--half: calc((var(--width-gutter) * .5));
  --width-gutter--compensation: calc((var(--width-gutter) * .5) * -1);
  --width-container--small: calc(var(--minWidth-media--small) + var(--width-gutter));
  --width-container--medium: calc(var(--minWidth-media--medium) + var(--width-gutter));
  --width-container--large: calc(var(--minWidth-media--large) + var(--width-gutter));
  --width-column: calc(100% / 12);
  --reset-box: {
    border: 0;
    margin: 0;
    padding: 0;
  };
  --spacer: {
    flex: auto;
  };
  --container: {
    margin-left: auto;
    margin-right: auto;
  };
  --container-flex: {
    align-items: center;
    border: 0;
    display: flex;
    height: 100%;
    justify-content: center;
    margin: 0;
    padding: 0;
  };
  --container-fluid: {
    margin-left: auto;
    margin-right: auto;
    padding-left: var(--margin-outter);
    padding-right: var(--margin-outter);
  };
  --row: {
    box-sizing: border-box;
    display: flex;
    flex: 0 1 auto;
    flex-direction: row;
    flex-wrap: wrap;
  };
  --width-column: {
    box-sizing: border-box;
    min-width: 0;
    padding-left: var(--width-gutter--half);
    padding-right: var(--width-gutter--half);
  };
  --width-column-auto: {
    box-sizing: border-box;
    flex: 1 0 0%;
    max-width: 100%;
    min-width: 0;
    padding-left: var(--width-gutter--half);
    padding-right: var(--width-gutter--half);
  };
  --width-column-1: {
    @apply --width-column;
    flex: 1 0 var(--width-column) !important; /* IE11 */
    max-width: var(--width-column) !important;
  };
  --width-column-2: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 2) !important; /* IE11 */
    max-width: calc(var(--width-column) * 2) !important;
  };
  --width-column-3: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 3) !important; /* IE11 */
    max-width: calc(var(--width-column) * 3) !important;
  };
  --width-column-4: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 4) !important; /* IE11 */
    max-width: calc(var(--width-column) * 4) !important;
  };
  --width-column-5: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 5) !important; /* IE11 */
    max-width: calc(var(--width-column) * 5) !important;
  };
  --width-column-6: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 6) !important; /* IE11 */
    max-width: calc(var(--width-column) * 6) !important;
  };
  --width-column-7: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 7) !important; /* IE11 */
    max-width: calc(var(--width-column) * 7) !important;
  };
  --width-column-8: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 8) !important; /* IE11 */
    max-width: calc(var(--width-column) * 8) !important;
  };
  --width-column-9: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 9) !important; /* IE11 */
    max-width: calc(var(--width-column) * 9) !important;
  };
  --width-column-10: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 10) !important; /* IE11 */
    max-width: calc(var(--width-column) * 10) !important;
  };
  --width-column-11: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 11) !important; /* IE11 */
