  };
  --width-column-12: {
    @apply --width-column;
    flex: 1 0 calc(var(--width-column) * 12) !important; /* IE11 */
    max-width: calc(var(--width-column) * 12) !important;
  };
  --start: {
    justify-content: flex-start !important;
    text-align: start;
  };
  --center: {
    justify-content: center !important;
    text-align: center;
  };
  --end: {
    justify-content: flex-end !important;
    text-align: end;
  };
  --text-center: {
    align-items: center;
    display: flex;
    justify-content: center;
  };
  --text-middle: {
    align-items: center;
    display: flex;
    justify-content: flex-start;
  };
  --top: {
    align-items: flex-start;
  };
  --middle: {
    align-items: center;
  };
  --bottom: {
    align-items: flex-end;
  };
  --row-break: {
    margin-left: calc(-1rem - 1px);
    margin-right: calc(-1rem - 1px);
  };
  --row-collapse: {
    padding-left: 0 !important;
    padding-right: 0 !important;
  };
  --flex-layout: {
    display: flex;
    flex-direction: column;
  };
  --reset-text-border: {
    border-style: solid;
    border-width: 0;
    box-sizing: inherit;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
  };
  --egrid-reset: {
    height: 100%;
    margin: 0;
    padding: 0;
    width: 100%;
  };
}

