  p, a, li, td, body, table, blockquote {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
  }

  p, a, li, td, blockquote {
    mso-line-height-rule: exactly;
  }

  table {
    border-collapse: collapse;
    mso-table-lspace: 0pt;
    mso-table-rspace: 0pt;
  }
}

.s-email__body {
  @apply --egrid-reset;
  overflow-y: hidden;
  overflow-x: auto;
}

.s-email__body-cell {
  @apply --egrid-reset;
  border-top: 0;
  padding: 10px;
}

.s-email__view {
  @apply --egrid-reset;
}

.o-email-container {
  border: 0;
  max-width: 600px !important;
  margin: 0 auto;

  @media (--md-up) {
    width: 600px !important;
  }
}

.o-email-preheader {
  border-top: 0;
  border-bottom: 0;
  padding-top: 9px;
  padding-bottom: 9px;
}
