/*---
title: Dropdowns
section: Components
---*/

/* DEPRECATED
 * .c-dropdown__toggle, .c-dropdown__text-toggle, .c-dropdown__content
 */

:root {
  --size-dropdown-select: 30px;
  --size-dropdown-icon: 24px;
  --size-dropdown-menu-icon: 16px;
  --offset-dropdown-menu-icon: 3px;
}

.c-dropdown {
  cursor: pointer;
  display: inline-block;
  height: auto;
  position: relative;
  z-index: 20;
}

.c-dropdown__select,
.c-dropdown__toggle {
  background-color: var(--color-control-background);
  border: 1px solid var(--color-control-border);
  border-radius: var(--global-rounded);
  color: var(--color-page);
  cursor: pointer;
  display: inline-block;
  font-size: var(--fontSize--small);
  height: var(--size-dropdown-select);
  position: relative;
  text-align: left;
  margin-right: .5rem;
  padding: 0 1.5rem 0 .5rem;
  text-decoration: none;

  &::before {
    @apply --triangle;
    @apply --triangle-bottom;
    border-width: 5px 4px 0;
    color: var(--color-page);
    content: '';
    position: absolute;
    right: .5rem;
    top: calc(var(--size-dropdown-select) / 2 - 3px);
  }

  &--large { height: 42px; }
}

.c-dropdown__toggle--large,
.c-dropdown__text-toggle {
  border: 2px solid transparent;
  border-radius: var(--global-rounded);
  color: var(--white);
  cursor: pointer;
  font-weight: 400;
  margin: 0;
  padding: 5px 5px 5px 0;
  text-transform: uppercase;
  @media (--md-up) {
    margin: 0 0 0 13px;
    padding: 5px 13px 5px 0;
  }
  &:--enter,
  &:--active {
    background-color: var(--nav-active-color);
    color: var(--nav-active-copy);

    &::before { border-left-color: var(--nav-active-copy); }
    .c-icon { color: var(--nav-active-copy); }
  }

  &:--active::before { transform: rotate(90deg); }

  &::before {
    @apply --arrow-right;
    color: rgba(255, 255, 255, .5);
    content: '';
    height: 4px;
    margin: -2px 8px 0 14px;
    transition-duration: .3s;
    width: 6px;
  }
}

.c-dropdown__icon {
  color: var(--color-primary);
  height: var(--size-dropdown-icon);
  margin-right: 8px;
  width: var(--size-dropdown-icon);
}

.c-dropdown__link {
  background-color: var(--color-dropdown-link-background);
  cursor: pointer;
  font-family: inherit;
  font-size: var(--fontSize--small);
  padding: 6px 14px 7px;
  text-align: left;
  transition: background-color var(--global-duration) ease, color var(--global-duration) ease;
  width: 100%;

  &:--enter { background-color: var(--color-dropdown-link-background-enter); }

  &:--active {
    background-color: var(--color-dropdown-link-background-active);
    color: var(--color-dropdown-link-active);
  }

  &:--disabled {
    box-shadow: none;
    opacity: .5;
  }
}

/* Menu Elements */

.c-dropdown__menu,
.c-dropdown__content {
  @apply --fade-out;
  background-color: var(--color-dropdown-menu-background);
  box-shadow: var(--shadow-dropdown);
  color: var(--color-dropdown-menu);
  border-radius: var(--global-rounded);
  display: inline-block;
  list-style: none;
  margin: 0;
  min-width: 100px;
  max-width: 100%;
  overflow: hidden;
  padding: 0;
  position: absolute;
  left: 0;
  top: 30px;
  z-index: 1;

  &:--active { @apply --fade-in; }

  &::before { right: 20px !important; }

  &::after,
  &:not(.is-active) svg { display: none; }

  .c-icon {
    color: var(--color-page);
    height: var(--size-dropdown-menu-icon);
    top: var(--offset-dropdown-menu-icon) !important;
    width: var(--size-dropdown-menu-icon);
  }

  &--overlap {
    left: 13px !important;
    top: -3px !important;
    width: 165px !important;
  }
  
  .c-dropdown--right & {
    left: auto;
    right: .5rem;
  }
}

.c-dropdown__group { border-bottom: 1px solid var(--borderColor-inactive); }

/* Large Dropdown */

/* DEPRECATED
 * .c-dropdown--picker, .c-dropdown__picker */

.c-dropdown--large,
.c-dropdown--picker {
  .c-dropdown__menu,
  .c-dropdown__content {
    min-width: 13.5rem;
    padding: var(--frame-third);
  }

  .c-button { margin-bottom: 0; }
}

.c-dropdown__viewer,
.c-dropdown__picker {
  box-shadow: var(--shadow-dropdown-picker);
  height: 178px;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 0 1rem;
  margin-bottom: var(--frame-quarter);
}

.c-dropdown--data {
  z-index: 5;

  & .c-dropdown__select,
  & .c-dropdown__toggle {
    color: var(--color-dropdown-data-select);
    min-width: 200px;
  }

  & .c-dropdown__list {
    @apply --fade-in;
    backround-color: var(--color-dropdown-menu-background);
    border: 1px dashed var(--grey-600);
    border-radius: 0 var(--global-rounded) var(--global-rounded) var(--global-rounded);
    box-shadow: none;
    color: var(--color-dropdown-menu);
    display: inline-block;
    left: 0;
    list-style: none;
    margin: 0;
    max-width: 100%;
    min-width: 200px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    top: 29px;
    z-index: -1;
  }
}