/*---
title: Field
section: Components
---*/

:root {
  --field-font-size: var(--fontSize--small);
  --field-min-height: 30px;
}

.c-field {
  background-color: transparent;
  border-bottom: solid var(--grey-500);
  border-width: 0 0 2px;
  border-radius: 0;
  color: var(--color-page);
  display: flex;
  font-family: inherit;
  font-size: var(--field-font-size);
  margin: 0 0 16px 0;
  min-height: var(--field-min-height); /* IE11 Input Height */
  padding: 2px 4px;
  transition: border var(--global-duration) ease-in-out;
  width: 100%;

  &:focus,
  &.has-focus {
    border-bottom-color: var(--blue-600);
    outline: none;
  }

  &:focus:invalid,
  &:focus.is-invalid { border-bottom-color: var(--orange-600); }

  &:invalid,
  &.is-invalid { border-bottom-color: var(--orange-600); }

  &:focus.is-error { border-bottom-color: var(--red-600); }

  &.is-error { border-bottom-color: var(--color-negative); }
}

.c-field--small { font-size: var(--fontSize--tiny); }

.c-field--medium { font-size: var(--fontSize--semiMedium); }

.c-field--large { font-size: var(--fontSize--semiLarge); }