/* ---
title: Badges
section: Components
---*/ 

/* DEPRECATED
 * .c-badge--round
 */

:root {
  --badge: {
    border-radius: 50px;
    color: var(--white);
    display: inline-block;
    font-size: var(--font-size--tiny);
    line-height: 1;
    max-width: 100%;
    outline: 0;
    padding: 3px 8px;
    position: relative;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    user-select: none;
    vertical-align: middle;
    white-space: nowrap;
  };
  --badge-background: var(--grey-600);
  --badge-default: transparent;
  --badge-positive: var(--green-600);
  --badge-informative: var(--blue-600);
  --badge-informative-alt: var(--magenta-500);
  --badge-live: var(--gold-700);
  --badge-alterable: var(--orange-600);
  --badge-negative: var(--red-600);
}

.c-badge {
  @apply --badge;
  background-color: var(--badge-background);

  &:empty { display: none; }

  &:not(:last-child) { margin-right: 6px; }
}

.c-badge--small {
  min-height: 14px;
  min-width: 14px;
}

.c-badge--round,
.c-badge--icon {
  font-size: 18px;
  font-weight: bold;
  min-height: 24px;
  min-width: 24px;
  padding: 3px;
}

.c-badge--default {
  background-color: var(--badge-default);
  color: var(--color-page);
}

.c-badge--positive { background-color: var(--badge-positive); }
.c-badge--informative { background-color: var(--badge-informative); }
.c-badge--live { background-color: var(--badge-live); }
.c-badge--alterable { background-color: var(--badge-alterable); }
.c-badge--negative { background-color: var(--badge-negative); }
.c-badge--purple { background-color: var(--badge-informative-alt); }
