/*---
title: Drawer
section: Objects
---*/

:root {
  --backgroundColor-drawer: var(--white);
  --shadow-drawer: 0 0 5px var(--color-shadow);
  --backgroundColor-drawer-header: var(--color-primary);
  --borderColor-drawer: var(--borderColor);
  --color-drawer-header: var(--white);
}

.o-drawer {
  background-color: var(--backgroundColor-drawer);
  box-shadow: var(--shadow-drawer);
  position: fixed;
  top: 0;
  bottom: 0;
  width: 85%;
  max-width: 305px;
  z-index: 800;
}

.o-drawer--left {
  left: -100%;
  transition: left .3s ease;

  &.is-open { left: 0; }
}

.o-drawer--right {
  right: -100%;
  transition: right var(--global-duration) ease-in-out;

  &.is-open { right: 0; }
}

.o-drawer__close {
  @apply --fade-out;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 30 !important;

  .is-open + & { @apply --fade-in; }
}

.o-drawer__header {
  background-color: var(--backgroundColor-drawer-header);
  color: var(--color-drawer-header);
  height: 58px;
  padding: 0 1em;
}

.o-drawer__brand {
  @apply --hide-text;
  /* stylelint-disable function-url-quotes */
  background-image: url(var(--brand-logo));
  /* stylelint-enable */
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  height: 48px;
  margin: .5em 0 0;
  position: relative;
  width: 90px;
}

.o-drawer__product {
  display: inline-block;
  margin: .5em 0 0 1em;
  font-weight: normal;
  position: relative;
  text-transform: uppercase;
  vertical-align: middle;
}

.o-drawer__group {
  &:not(:last-child) { border-bottom: var(--borderColor-drawer) 1px solid; }
}

.o-drawer__action {
  background-color: var(--white);
  display: inline-block;
  padding: 1em;
  transition: background-color var(--global-duration) ease-in-out;
  text-align: left;
  width: 100%;

  &:--active { background-color: var(--grey-100); }

  .c-icon {
    color: var(--grey-700);
    margin-right: .5em;
  }
}

.o-drawer__account-name { font-size: var(--fontSize--semiMedium); }
